import "./api.js";
// import * as google from "googleapis";
import {NgModule, ModuleWithProviders} from "@angular/core";
import { YTApiConfig, initConfigs } from "./config/YTApiConfig";
import { YTApiService } from "./YTApiService";
import { YTApiProvideService } from "./YTApiProviderService";


  export class YTApiModule {
    static setConfig(apiConfig: initConfigs): ModuleWithProviders  {

      let YTP = new YTApiProvideService(apiConfig);

      YTP.handleClientLoad();

      return {
        ngModule: YTApiModule,
        providers: [
          {
            provide: YTApiService,
            useFactory:function(){ return YTApiService.factory(apiConfig) }
          }
        ]
      };
    }
  static decorators: DecoratorInvocation[] = [
{ type: NgModule },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
];
}


interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
